.HomePage{
    color:rgb(252, 0, 0);
}

.background {
    background-image: linear-gradient(120deg, #77179c 0%, #e44fd0 100%);
    background-image: url(./Assests/HomePageBG.png);
    min-height: 120%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
background {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

}

canvas {
    width: 60;
}

.typewriter {
    margin-top: 0%;
    text-align: center;
    overflow: hidden;
    color: rgb(245, 245, 245);
    font-family: Courier New;
    top: 30%;
    font-size: 60px;
    white-space: nowrap;
    letter-spacing: .20em;
    animation:
        typing 3.0s steps(30, end),
        blink-caret .75s step-end infinite;
}

/* The typing effect */
@keyframes typing {
    from {
        width: 0%
    }

    to {
        width: 100%
    }
}


@keyframes blink-caret {

    from,
    to {
        border-color: transparent
    }
}
